* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
section {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  display: flex;
  justify-self: center;
  align-items: center;
}
section::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 200px;
  background: linear-gradient(to top, #fff, transparent);
  z-index: 1000;
}
section img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  pointer-events: none;
}
section img #boy {
  transform-origin: bottom;
}
section #text {
  position: relative;
  color: #fff;
  font-size: 10vw;
}
.sec {
  position: relative;
  padding: 100px;
}
.sec h2 {
  font-size: 3.5em;
  margin-bottom: 10px;
  color: #20496a;
}
.sec p {
  font-size: 1.2em;

  color: #20496a;
}
