* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

section {
  width: 100%;
  height: 100vh;
  justify-self: center;
  align-items: center;
  display: flex;
  position: relative;
  overflow: hidden;
}

section:before {
  content: "";
  width: 100%;
  height: 200px;
  z-index: 1000;
  background: linear-gradient(to top, #fff, rgba(0, 0, 0, 0));
  position: absolute;
  bottom: 0;
  left: 0;
}

section img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  pointer-events: none;
  position: absolute;
  top: 0;
  left: 0;
}

section img #boy {
  transform-origin: bottom;
}

section #text {
  color: #fff;
  font-size: 10vw;
  position: relative;
}

.sec {
  padding: 100px;
  position: relative;
}

.sec h2 {
  color: #20496a;
  margin-bottom: 10px;
  font-size: 3.5em;
}

.sec p {
  color: #20496a;
  font-size: 1.2em;
}

